}

.links:hover{
 /* color:rgb(121, 189, 252); */
 color:rgb(121, 189, 252);
 text-decoration: underline;
}
.fas:hover{
  color:rgb(41, 152, 255);
  text-decoration: underline;
 }
 ul{
   list-style: none;
 }
  .h1_links:hover{
  color: #000;
 }
