.custom-img {
    height: 100vh;
    object-fit: cover;
}
.corousal{
    overflow-y: hidden;
}
.focus {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  /* background-color:#ABABFB; */
  margin-top: 1em;
  margin-bottom: 1em;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.486);
  /* margin: 1em; */
  height: 6em;
}

.focus:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}


.image-style {
  width: 3em;
}

.font-details {
  font-size: 3em; 
  font-weight: 500;
